@font-face {
  font-family: "Open Sans";
  src: local("Open Sans") url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: local("Open Sans") url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: #000000;
  font-weight: bold;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .wrapper {
    min-width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .wrapper {
    width: 1150px;
  }
}
.btn {
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.btn:hover {
  color: #ffffff;
  background-color: #444444;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(170, 169, 169, 0.5);
}
.reset {
  margin: 0;
  padding: 0;
  list-style: none;
}
.about-us-item-pseudo {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.mobile-header {
  font-size: 27px;
  line-height: 30px;
}
.tablet-header {
  font-size: 43px;
  line-height: 50px;
}
.desktop-header {
  font-size: 53px;
  line-height: 57px;
}
.placeholder::-webkit-input-placeholder {
  color: rgba(68, 68, 68, 0.3);
}
.placeholder:-moz-placeholder {
  color: rgba(68, 68, 68, 0.3);
}
.placeholder::-moz-placeholder {
  color: rgba(68, 68, 68, 0.3);
}
.placeholder:-ms-input-placeholder {
  color: rgba(68, 68, 68, 0.3);
}
/* mobile */
.page-header__delivery {
  display: none;
}
.page-header__wrapper {
  margin: 0 auto;
}
.page-header__logo {
  display: block;
  width: 100%;
  padding-top: 22px;
  padding-bottom: 15px;
  text-align: center;
  border-bottom: 1px solid #ececec;
}
.page-header__logo:hover {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}
.user-block {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto;
}
.user-block__item {
  border-bottom: 1px solid #ececec;
}
.user-block__link {
  position: relative;
  display: block;
  padding-top: 27px;
  padding-bottom: 27px;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-align: center;
}
.user-block__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.user-block__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.user-block__link svg {
  position: absolute;
  opacity: 0.2;
}
.user-block__link--search svg {
  margin-right: 26px;
  margin-bottom: -4px;
  margin-left: -64px;
}
.user-block__link--cart svg {
  margin-right: 26px;
  margin-bottom: -4px;
  margin-left: -43px;
}
.user-block__link:hover svg {
  opacity: 1;
  fill: #63d1bb;
}
.user-block__link:active svg {
  fill: rgba(99, 209, 187, 0.3);
}
/* tablet */
@media (min-width: 768px) {
  .page-header {
    height: 88px;
    border-bottom: 1px solid #ececec;
    background-color: #ffffff;
  }
  .page-header__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    min-width: 768px;
    padding: 0 50px;
  }
  .page-header__logo {
    order: -2;
    width: 112px;
    box-sizing: content-box;
    padding-top: 22px;
    padding-right: 209px;
    padding-bottom: 5px;
    border: none;
  }
  .user-block {
    display: flex;
    order: -1;
    margin: 0;
  }
  .user-block__item {
    border: none;
  }
  .user-block__link svg {
    margin: 0;
  }
  .user-block__link--search {
    font-size: 0;
    line-height: 0;
  }
  .user-block__item--search {
    padding-right: 40px;
    padding-left: 40px;
    border-left: 1px solid #ececec;
    border-right: 1px solid #ececec;
  }
  .user-block__link--cart {
    padding-top: 31px;
    padding-bottom: 32px;
    padding-left: 83px;
  }
  .user-block__link--search svg {
    top: 35px;
    left: -10px;
  }
  .user-block__link--cart svg {
    top: 35px;
    left: 31px;
  }
  .page-header__delivery {
    position: absolute;
    top: 52%;
    right: 6.5%;
    display: block;
    margin: 0;
    padding-top: 25px;
    font-size: 15px;
    font-weight: normal;
    color: #444444;
  }
}
/* desktop */
@media (min-width: 1150px) {
  .page-header__wrapper {
    width: 1150px;
  }
  .page-header__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 149px;
    padding-top: 9px;
    padding-right: 10px;
    padding-bottom: 13px;
    padding-left: 10px;
  }
  .user-block {
    order: 1;
    height: 88px;
  }
  .page-header__delivery {
    top: 52%;
    right: 4.5%;
    padding-top: 25px;
  }
}
/* mobile */
.main-nav {
  position: relative;
  min-width: 320px;
  margin: 0 auto;
}
.main-nav__link {
  display: block;
  padding-top: 27px;
  padding-bottom: 27px;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-align: center;
  border-bottom: 1px solid #ececec;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__link--arrival,
.main-nav__link--sale {
  display: none;
}
.main-nav__toggle {
  position: absolute;
  top: 31px;
  right: 32px;
  display: block;
  width: 22px;
  height: 22px;
  border: none;
  outline: none;
}
.main-nav__toggle--open {
  position: absolute;
  top: 29px;
  right: 30px;
  display: block;
  width: 22px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-menu-close.svg");
  background-repeat: no-repeat;
  outline: none;
}
.main-nav__toggle--close {
  position: absolute;
  top: 32px;
  right: 31px;
  display: block;
  width: 22px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-menu-open.svg");
  background-repeat: no-repeat;
  outline: none;
}
@media (max-width: 767px) {
  .mobile-menu-closed {
    display: none;
  }
}
/* tablet */
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav {
    display: flex;
    flex-wrap: wrap;
    max-width: 50%;
    margin: 0;
    min-width: 400px;
  }
  .main-nav__link {
    padding-top: 20px;
    font-size: 15px;
    border: none;
  }
  .main-nav__link--catalog {
    padding-right: 35px;
  }
}
/* desktop */
@media (min-width: 1150px) {
  .main-nav {
    padding-right: 70px;
  }
  .main-nav__link--catalog {
    padding-top: 32px;
    padding-right: 35px;
    font-size: 17px;
    border-right: 1px solid #ececec;
  }
  .main-nav__link--order {
    padding-top: 32px;
    padding-left: 35px;
    font-size: 17px;
  }
  .main-nav__link--sale,
  .main-nav__link--arrival {
    display: block;
    padding-top: 22px;
  }
  .main-nav__link--arrival {
    padding-right: 60px;
  }
}
/* mobile */
.page-main__img {
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -15%;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-main__img {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}
.page-main__img .wrapper {
  padding: 0;
}
.page-main__title {
  display: block;
  margin: 0 auto;
  width: 244px;
  padding-top: 71px;
  padding-bottom: 122px;
  font-size: 27px;
  line-height: 30px;
  color: #010000;
  text-align: center;
}
/* tablet */
@media (min-width: 768px) {
  .page-main__img {
    background-image: url("../img/hero-tablet@1x.jpg");
  }
  .page-main__img .wrapper {
    padding: 0 50px;
  }
  .page-main__title {
    font-size: 43px;
    line-height: 50px;
    width: 510px;
    padding-top: 155px;
    padding-bottom: 372px;
    color: #000000;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .page-main__img {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 1150px auto;
  }
}
/* desktop */
@media (min-width: 1150px) {
  .page-main__img {
    background-image: url("../img/hero-desktop@1x.jpg");
  }
  .page-main__title {
    font-size: 53px;
    line-height: 57px;
    width: 615px;
    padding-bottom: 359px;
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi), (min-width: 1150px) and (min-resolution: 1.5dppx) {
  .page-main__img {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 1150px auto;
  }
}
/* mobile */
.categories__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 19px;
  color: #ffffff;
}
.categories__item {
  position: relative;
}
.categories__link {
  display: block;
  width: 131px;
  padding-top: 26px;
  padding-bottom: 29px;
  padding-left: 28px;
  color: #ffffff;
}
.categories__link--interior svg {
  position: absolute;
  top: 23px;
  right: 37px;
  width: 40px;
  height: 62px;
  fill: #149080;
  opacity: 0.5;
}
.categories__link--toys svg {
  position: absolute;
  top: 21px;
  right: 27px;
  width: 60px;
  height: 60px;
  fill: #149080;
  opacity: 0.5;
}
.categories__item--interior {
  background-color: #63d1bb;
}
.categories__item--interior:hover {
  background-color: #59d5bc;
}
.categories__item--interior:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #59d5bc;
}
.categories__item--toys {
  background-color: #56c3b2;
}
.categories__item--toys:hover {
