}
.categories__item--toys:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #46c1ae;
}
.categories__link--interior:hover svg,
.categories__link--toys:hover svg {
  opacity: 0.7;
}
.categories__link--interior:active svg,
.categories__link--toys:active svg {
  opacity: 0.3;
}
/* tablet = desktop */
@media (min-width: 768px) {
  .categories__list {
    display: flex;
    font-size: 20px;
    line-height: 30px;
  }
  .categories__item {
    width: 50%;
  }
  .categories__item:hover {
    margin-top: -5px;
  }
  .categories__link {
    padding-top: 28px;
    padding-bottom: 31px;
    padding-left: 35px;
  }
  .categories__link--interior svg {
    top: 30px;
  }
  .categories__link--toys svg {
    top: 30px;
    right: 35px;
  }
}
/* mobile */
.promo {
  min-width: 320px;
  padding-top: 27px;
}
.promo__title {
  margin: 0;
  margin-bottom: 5px;
  font-size: 25px;
  line-height: 30px;
}
.promo__label {
  margin: 0;
  margin-bottom: 23px;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}
.promo__info {
  display: flex;
  flex-direction: column;
}
.promo__description {
  margin: 0;
  margin-bottom: 15px;
  color: #444444;
  font-weight: normal;
}
.promo__table {
  line-height: 20px;
  color: #444444;
  font-weight: normal;
  text-align: left;
  border-collapse: collapse;
}
.promo__table tr {
  border-bottom: 1px solid #ececec;
}
.promo__value {
  width: 95px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: normal;
}
.promo__field {
  padding-top: 10px;
  padding-bottom: 10px;
}
.promo__img {
  width: 100%;
  margin: 0;
  margin-top: 28px;
  margin-bottom: 18px;
}
.promo__price {
  display: block;
  margin-bottom: 26px;
  font-size: 17px;
  color: #444444;
  text-align: center;
}
.promo__btn {
  display: flex;
  justify-content: center;
  min-width: 256px;
  padding-top: 14px;
  padding-bottom: 14px;
}
/* tablet */
@media (min-width: 768px) {
