  }
  .promo__title {
    font-size: 43px;
    line-height: 50px;
    margin: 0;
    margin-left: -3px;
  }
  .promo__label {
    width: 70px;
    margin: 0;
    margin-right: 30px;
    padding-top: 11px;
    line-height: 17px;
    text-align: center;
  }
  .promo__info {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    min-width: 668px;
    padding-top: 50px;
  }
  .promo__description {
    min-width: 282px;
    margin-right: 400px;
    margin-bottom: 34px;
    font-size: 17px;
    line-height: 30px;
  }
  .promo__table {
    min-width: 282px;
    margin-right: 400px;
    margin-bottom: 55px;
  }
  .promo__price {
    margin-bottom: 33px;
    padding-left: 72px;
