/* mobile */

.recommends {
  padding-top: 55px;
}

.recommends__title {
  margin: 0;
  margin-bottom: 24px;

  font-size: 25px;
  line-height: 30px;
}

.recommends__list {
  .reset;
}

.recommends__text {
  line-height: 30px;
  color: #444444;
  font-weight: normal;
}

.recommends__author {
  margin: 0;
  margin-top: 25px;

  font-size: 17px;
  color: #000000;
  font-weight: bold;
}

.recommends__nickname {
  font-size: 17px;
  line-height: 30px;
  font-weight: normal;
}

.recommends__slider-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 36px;
}

.recommends__prev,
.recommends__next {
  display: block;
  padding-top: 9px;
  padding-right: 38px;
  padding-bottom: 9px;
  padding-left: 38px;

  border: @grey-border;
}

.recommends__prev:hover svg,
.recommends__next:hover svg {
  opacity: 0.6;
}

.recommends__prev:active svg,
.recommends__next:active svg {
  opacity: 0.3;
}

.recommends__btn {
  display: flex;
  justify-content: center;
  min-width: 256px;
  padding-top: 14px;
  padding-bottom: 14px;
}

/* tablet */

@media (min-width: @tablet-width) {
  .recommends {
    padding-top: 33px;
  }

  .recommends .wrapper {
    background-image: url("../img/review@1x.jpg");
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .recommends .wrapper {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px 457px;
    }
  }

  .recommends__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 77px;
  }

  .recommends__title {
    .tablet-header;

    margin: 0;
    margin-left: -4px;
  }

  .recommends__text {
    min-width: 642px;
    margin-top: 45px;

    font-size: 17px;
  }

  .recommends__author {
    padding-top: 30px;

    font-size: 18px;
  }

  .recommends__list {
    order: 2;
    padding-bottom: 53px;
  }

  .recommends__btn {
    order: 1;
    min-width: 175px;
    margin-top: 2px;
  }

  .recommends__slider-button {
    order: 4;
    margin-top: -95px;
    margin-left: 511px;
    margin-bottom: 0;
  }

  .recommends__prev {
    padding: 0;
    padding-right: 19px;

    border: none;
  }

  .recommends__next {
    padding: 0;
    padding-left: 19px;

    border: none;
  }
}

/* desktop */

@media (min-width: @desktop-width) {
  .recommends {
    padding-top: 26px;
  }

  .recommends .wrapper {
    background-position: 50px 0;
  }

  .recommends__wrapper {
    padding-top: 0;
  }

  .recommends__title {
    .desktop-header;

    margin-left: 172px;
    padding-top: 71px;
  }

  .recommends__list {
    min-width: 768px;
    margin-left: 175px;
  }

  .recommends__text {
    margin-right: 105px;
    padding-bottom: 100px;
  }

  .recommends__author {
    margin: 0;
    padding-top: 34px;
  }

  .recommends__btn {
    margin-top: 80px;
  }

  .recommends__slider-button {
    margin-top: -193px;
    margin-bottom: 0;
    margin-left: 893px;
  }
}
